import { Component, h, Prop, Element, Method } from '@stencil/core';
import { common } from '../../global/data/common';
import InputInterface from './inputInterface';
@Component({
    tag: 'ks-input-number',
    styleUrl: 'inputNumber.css'
})
export class InputNumber implements InputInterface {

    @Prop() name: string;
    @Prop() placeholder: string;
    @Prop() label: string;

    @Prop({reflect: true}) center: boolean;
    @Prop({reflect: true}) emphasis: boolean;

    @Prop() min: number;
    @Prop() max: number;
    @Prop() required: boolean;

    @Prop() step: number;
    @Prop() value: number;

    @Element() root: HTMLElement;

    @Prop() nomessage: boolean = false;
    @Prop({mutable: true, reflect: true}) novalidate: boolean = false;
    @Prop({mutable: true, reflect: true}) message: string = "";
    @Prop({mutable: true, reflect: true}) invalid: boolean = false;

	render() {
        return[
            this.label ?
                <label>{this.label}</label>
            : null,

            <input name={this.name} type="number" value={this.value}
                placeholder={this.placeholder}
                step={this.step}
                onInput={this.InputHandler}
                onChange={this.ChangeHandler}
                onBlur={this.ChangeHandler}
            />
            ,
            
            this.invalid && !this.nomessage ?
                <p>{this.message}</p>
            : null
        ];
    }

    componentDidLoad() {
        this.root.querySelector('input').value = this.value.toString();
    }

    InputHandler = (event: InputEvent) => {
        if(event.inputType != "insertText") {
            const input = this.root.querySelector("input");
            this.ClampInput(input);
        }
        event.stopImmediatePropagation();
    } 

    ChangeHandler = (event: Event) => {
        if(!this.novalidate) this.Validate();
        event.stopImmediatePropagation();
    } 

    @Method()
    async IsValid() {
        return this.message == "" ? true : false;
    }

    @Method()
    async Validate() {
        const strings = common.get("translations");
        const input = this.root.querySelector("input");

        let valid = true;
        let message = "";

        this.ClampInput(input);

        if(this.required) {
            if(input.value.length == 0) {
                valid = false;
                message = strings.inputFieldRequired;
            }
        }

        this.invalid = !valid;
        this.message = message;

        return Promise.resolve();
    }

    ClampInput(input: HTMLInputElement){
        if(parseInt(input.value) > this.max) {
            input.value = this.max.toString();
        }

        if(parseInt(input.value) < this.min) {
            input.value = this.min.toString();
        }
    }
}
