import { Component, h, Prop, State, Method } from '@stencil/core';
import { cart } from '../../../global/data/cart';
import * as cartFunctions from "../../../global/functions/cart";

@Component({
  tag: 'ks-cart-discount-points',
  styleUrl: 'cartDiscountPoints.css'
})
export class CartDiscountPoints {
	@Prop() placeholder: string = "";
	@Prop() message: string = "";
	@Prop() points: number = 1;
	@Prop() orderPoints: number = 1;

    discountPointsAddHandler(event: Event) {
		event.preventDefault();

		if(!this.loading) {
			const form = event.target as HTMLFormElement;
			const input = form.querySelector('input[name="pointCount"]') as HTMLInputElement;
			const value = input.value;

			if(value) {
				const valueNumber = parseInt(value.toString());

				if(!Number.isNaN(valueNumber)){
					this.loading = true;
					cartFunctions.discountPointsAdd(valueNumber);
				}
			}
		}
	}
	
	@State() loading: boolean = false;
	@Method() async ResetLoading() {
		this.loading = false;
	}

  	render() {
		const strings = cart.get("strings");

    	return[
			<form onSubmit={(e)=>this.discountPointsAddHandler(e)} novalidate>
				<ks-input-number
					center
					min={1} max={this.points}
					step={10}
					value={this.points}
					name="pointCount"
				></ks-input-number>

				<button type="submit">
					{this.loading ? <ks-loader/> : <span>{strings.discountPointsActivate}</span>}
				</button>
			</form>,

			<div class="points-message">+{this.orderPoints}</div>,
			<div class="info-message">{this.message}</div>
    	];
  	}
}