import { Component, h, Prop, State } from '@stencil/core';
import priceFormat from '../../../global/functions/price';
import * as cartFunctions from "../../../global/functions/cart";


@Component({
  tag: 'ks-cart-discount-ticket',
  styleUrl: 'cartDiscountTicket.css'
})
export class CartDiscountTicket {

	@Prop() name: string = "";
  @Prop() value: string = "";
  
  discountRemoveHandler() {
    cartFunctions.discountRemove();
  }

  @State() loading: boolean = false;

  	render() {
      const parsed = parseFloat(this.value);
      if(!isNaN(parsed)) this.value = priceFormat(parsed);

    	return[
            <div class="content ks-text-decorated">
                <div class="name">{this.name}</div>
                <div class="value">{this.value}</div>
            </div>,
            
            <div class="remove" onClick={()=>this.discountRemoveHandler()}>
                {this.loading ? <ks-loader/> : <ks-icon name="x"/>}
            </div>
    	];
  	}
}