ks-cart-discount-ticket {
    display: flex;

    width: 100%;
    min-height: 50px;

    box-sizing: border-box;
    font-family: var(--font-emphasis);

    background-color: #ff3838;
    color: white;

    
}

ks-cart-discount-ticket .content {
    flex: 1;

    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 10px 20px;
}

ks-cart-discount-ticket .name {
    flex: 1;
    font-weight: 700;
    font-size: 20px;
}

ks-cart-discount-ticket .remove {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;

    width: 50px;

    background-color: rgba(0, 0, 0, 0.068);;
    transition: background-color 0.2s ease;
}

ks-cart-discount-ticket .remove:hover {
    background-color: rgba(0, 0, 0, 0.137);
}

ks-cart-discount-ticket .remove:active {
    background-color: rgba(0, 0, 0, 0.281);
}

@media only screen and (max-width: 560px) {
    ks-cart-discount-ticket .content {
        display: block;
    }

    ks-cart-discount-ticket .value {
        font-size: 16px;
    }
}
