ks-cart-discount-points-message {
    display: block;
}

ks-cart-discount-points-message .heading {
    display: flex;
    justify-content: center;
    align-items: center;

    box-sizing: border-box;

    height: 40px;
    padding: 4px 5px !important;

    font-family: var(--font-emphasis);

    background-color: #f8f8f8;
    border: #dbdbdb solid 1px;
    color: #858585;

    font-size: 17px !important;
    font-weight: 700 !important;
}

ks-cart-discount-points-message > .info-message {
    text-align: center;
    font-size: 14px;
    margin-top: 10px;
}

ks-cart-discount-points-message > .points-message {
    position: absolute;
    top: -7px;
    right: 0;

    font-family: var(--font-emphasis);
    font-size: 24px;
    font-weight: 700;
}