ks-cart-discount-points {
    display: block;
}

ks-cart-discount-points > form {
    display: flex;
}

ks-cart-discount-points button {
    padding: 0 20px !important;
    
    position: relative;
    height: 40px;
    width: 90px;
    min-width: 90px;

    appearance: none;
    border: none;
    color: #ffffff;
    background-color: #242424;
    transition: background-color 0.3s ease;
}

ks-cart-discount-points button:hover {
    background-color: #363636;
}

ks-cart-discount-points button:active {
    background-color: #464646;
}

ks-cart-discount-points ks-input-number {
    width: 100%;
    margin-bottom: 0;
}

ks-cart-discount-points > .info-message {
    text-align: center;
    font-size: 14px;
    margin-top: 10px;
}

ks-cart-discount-points > .points-message {
    position: absolute;
    top: -7px;
    right: 0;

    font-family: var(--font-emphasis);
    font-size: 24px;
    font-weight: 700;
}