ks-input-number {
    display: block;
    margin-bottom: 15px;
}

ks-input-number label {
    display: block;
    margin-bottom: 3px;

    color: #000000;
    font-size: 14px;
}

ks-input-number[invalid] label {
    color: var(--color-secondary-darker);
}


ks-input-number input {
    height: 40px;
    vertical-align: middle;
    display: inline-block;

    max-width: 100%;
    width: 100%;
    padding: 0 10px;
    background: #fafafa;
    color: #373737;
    border: 1px solid #b2b2b2;
    overflow: visible;

    box-sizing: border-box;
    border-radius: 0;
    font: inherit;
}

ks-input-number input:focus {
    outline: none;
    background-color: #f4f4f4;
    color: #373737;
    border-color: #222222;
}

ks-input-number[emphasis] input {
    border-color: black;
    background-color: white;
}

ks-input-number[invalid] input {
    border-color: var(--color-secondary-darker);
    color: var(--color-secondary-darker);
}

ks-input-number[center] input {
    text-align: center !important;
}

ks-input-number[large] input {
    font-size: 17px;
    height: 55px;
    padding-left: 12px;
    padding-right: 12px;
}

ks-input-number[invalid] p {
    margin-top: 5px;
    color: var(--color-secondary-darker);
}

ks-input-number[invalid] input::placeholder {
    color: var(--color-secondary);
}


ks-input-number .wrapper {
    position: relative;
}

ks-input-number .wrapper input {
    padding-left: 45px;
}

ks-input-number .wrapper ks-icon {
    position: absolute;
    top: 50%;
    left: 12px;
    transform: translateY(-50%);
}

ks-input-number[value=""] .wrapper ks-icon,
ks-input-number:not([value]) .wrapper ks-icon {
    color: #aaaaaa;
}

ks-input-number[invalid] .wrapper ks-icon {
    color: var(--color-secondary);
}