import { Component, h, Prop } from '@stencil/core';

@Component({
  tag: 'ks-cart-discount-points-message',
  styleUrl: 'cartDiscountPointsMessage.css'
})
export class CartDiscountPointsMessage {
	@Prop() heading: string = "";
	@Prop() message: string = "";
	@Prop() points: number = 1;

  	render() {
    	return[
			<div class="heading">
				{this.heading}
			</div>,

			<div class="points-message">+{this.points}</div>,
			<div class="info-message">{this.message}</div>
        ];
  	}
}