ks-cart-discount-code {
    display: block;
}

ks-cart-discount-code > form:first-child {
    display: flex;
}

ks-cart-discount-code ks-input-text {
    display: block;
    flex: 1;
}

ks-cart-discount-code button {
    padding: 0 20px !important;
    height: 40px;
    width: 90px;
    position: relative;

    appearance: none;
    border: none;

    color: #ffffff;
    background-color: #242424;
    transition: background-color 0.3s ease;
}

ks-cart-discount-code button:hover {
    background-color: #363636;
}

ks-cart-discount-code button:active {
    background-color: #464646;
}


ks-cart-discount-code .message {
    cursor: pointer;

    margin-top: 7px;

    color: #222222; 
    transition: color 0.3s ease;
}

ks-cart-discount-code .message:hover {
    color: #303030;
}

@media only screen and (max-width: 470px) {
    ks-cart-discount-code .message {
        height: 25vw;
    }
}